import React from "react";
import { Link } from "react-router-dom";
import { MdAngBelow, LgToXl } from "./../useMedia";

const items = [
  {
    url: "/watch/326003",
    img:
      "https://files.cinerama.uz/images/banners/2021/07/12/efbde475-5ee4-4b70-8d53-b23a8d2448f5.png",
  },
  {
    url: "/watch/325366",
    img:
      "https://files.cinerama.uz/images/banners/2021/05/26/3f093a7d-2a94-4f61-93c1-9e9515ab643d.png",
  },
  {
    url: "/watch/324565",
    img:
      "https://files.cinerama.uz/images/banners/2021/04/20/d6c7e86e-3390-4252-af70-c9a5b1120306.png",
  },
  {
    url: "/watch/326715",
    img:
      "https://files.cinerama.uz/images/banners/2021/09/07/dddeea03-cd1f-48f6-a291-b118bb266109.png",
  },
  {
    url: "/watch/326401",
    img:
      "https://files.cinerama.uz/images/banners/2021/09/06/914e2576-ae17-40dc-87ae-a2d31945e471.png",
  },
];

const Content = () => {
  return (
    <ul className="uk-slideshow-items">
      {items.map((el, idx) => {
        return (
          <li key={idx + "img"}>
            <Link to={el.url}>
              <img src={el.img} alt={idx + "img"} data-uk-cover />
            </Link>
          </li>
        );
      })}
    </ul>
  );
};

const Slideshow = () => {
  return (
    <>
      <LgToXl>
        <div
          className="uk-position-relative uk-visible-toggle uk-light"
          tabIndex="-1"
          data-uk-slideshow="max-height: 300; autoplay: true; autoplay-interval: 5000; "
        >
          <Content />
          <a
            className="uk-height-1-1 m-0 uk-position-absolute uk-flex uk-flex-middle uk-position-left bg-gradient-left px-4 uk-visible@m"
            href="#previous"
            data-uk-slidenav-previous
            data-uk-slideshow-item="previous"
          >
            {" "}
          </a>
          <a
            className="uk-height-1-1 m-0 uk-position-absolute uk-flex uk-flex-middle uk-position-right bg-gradient-right px-4 uk-visible@m"
            href="#next"
            data-uk-slidenav-next
            data-uk-slideshow-item="next"
          >
            {" "}
          </a>
        </div>
      </LgToXl>
      <MdAngBelow>
        <div
          className="uk-position-relative uk-visible-toggle uk-light"
          tabIndex="-1"
          data-uk-slideshow="max-height: 150; autoplay: true; autoplay-interval: 5000; "
        >
          <Content />
        </div>
      </MdAngBelow>
    </>
  );
};

export default Slideshow;
