import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router";
import { LgToXl, MdAngBelow } from "./../components/useMedia";
import { getItem, getFiles } from "./../utils/api";
import { DefaultPlayer as Video } from "react-html5video";
import PageLoading from "../components/Loading/PageLoading";
import MovieInfo from "../components/MovieInfo/MovieInfo";
import SerialsItem from "../components/MovieItem/SerialsItem";
import "react-html5video/dist/styles.css";
import { Link } from "react-router-dom";
import { Context } from "./../App";

const defaultMovie = "https://files.itv.uz/assets/help/subscription_needed.mp4";

const Watch = () => {
  const { id } = useParams();
  const { user } = useContext(Context);
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState(null);
  const [file, setFile] = useState(null);
  const [poster, setPoster] = useState(null);
  const defaultPoster = file && file[0].image;

  useEffect(() => {
    getItem(id.slice(1), id.slice(0, 1), setData);
  }, [id]);

  useEffect(() => {
    getFiles(id.slice(1), id.slice(0, 1), setFile).then(() => {
      setLoading(false);
      setPoster(defaultPoster);
    });
  }, [id, defaultPoster]);

  return (
    <>
      <PageLoading loading={loading} />
      {data && (
        <div className="m-3 m-md-4" data-uk-height-viewport="expand: true">
          {user ? (
            <div className="uk-flex uk-child-width-1-1 video-content">
              <Video
                className="uk-width-auto"
                controls={["PlayPause", "Seek", "Time", "Volume", "Fullscreen"]}
                poster={poster}
                onCanPlayThrough={() => {}}
              >
                <source src={defaultMovie} />
              </Video>
              {data.isSerial && (
                <LgToXl>
                  <div className="uk-width-1-3@s uk-width-1-1 serial-items-block">
                    {file &&
                      file.map((el) => (
                        <SerialsItem
                          classNames="mx-3 mt-3 serial-items"
                          key={el.id}
                          image={el.image}
                          episode={el.order}
                          poster={poster}
                          setPoster={setPoster}
                        />
                      ))}
                  </div>
                </LgToXl>
              )}
            </div>
          ) : (
            <div className="no-auth-poster">
              <img src={poster} alt="poster" />
              <Link to="/login">
                <span className="fa fa-2x fa-play"></span>
              </Link>
            </div>
          )}
          {user && data.isSerial && (
            <MdAngBelow>
              <div className="uk-light pt-3 px-3 movie-info">
                <ul className="mb-0" uk-accordion="multiple: true">
                  <li>
                    <a className="uk-accordion-title pb-3" href="#open">
                      Показать все серии
                    </a>
                    <div className="uk-accordion-content toggle-serial-items-block uk-child-width-1-1 uk-child-width-1-2@s uk-grid">
                      {file &&
                        file.map((el) => (
                          <SerialsItem
                            classNames="toggle-serial-items uk-grid-margin"
                            key={el.id}
                            image={el.image}
                            episode={el.order}
                            poster={poster}
                            setPoster={setPoster}
                          />
                        ))}
                    </div>
                  </li>
                </ul>
              </div>
            </MdAngBelow>
          )}
          {data && (
            <MovieInfo
              poster={data?.poster}
              title={data?.title}
              titleEn={data?.titleEn}
              imdb={data?.rating?.imdb?.rating}
              kp={data?.rating?.kp?.rating}
              year={data?.year}
              countries={data?.countries}
              genres={data?.genres}
              slogan={data?.slogan}
              description={data?.description}
            />
          )}
        </div>
      )}
    </>
  );
};

export default Watch;
