import React from "react";
import { Formik } from "formik";
import { Link } from "react-router-dom";
import * as yup from "yup";
import InputText from "./../components/Form/InputText";
import InputPassword from "./../components/Form/InputPassword";
import { signIn, googleSignIn } from "../utils/firebaseAuth";
import { useNavigate } from "react-router";

const Login = () => {
  let schema = yup.object().shape({
    emailLogin: yup
      .string()
      .email("Неверный адрес электронной почты")
      .required("Данное поле является обязательным"),
    passwordLogin: yup
      .string()
      .min(6, "Должно быть не менее 6 символов")
      .matches(/^[A-zА-я0-9_ ]+$/, "Удалите ненужные символы")
      .required("Данное поле является обязательным"),
  });

  const navigate = useNavigate();

  return (
    <div className="uk-position-fixed uk-width-1-1 uk-height-viewport uk-position-top-left login-page">
      <Formik
        initialValues={{ emailLogin: "", passwordLogin: "" }}
        validateOnBlur
        onSubmit={({ emailLogin, passwordLogin }) => {
          signIn(emailLogin, passwordLogin).then(() => {
            console.log("Sign In");
            navigate("/");
          });
        }}
        validationSchema={schema}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isValid,
        }) => (
          <form className="uk-form-stacked m-3">
            <h2 className="uk-light uk-text-semibold">Войти</h2>
            <InputText
              label="E-mail"
              placeholder="Введите ваш e-mail адрес"
              name="emailLogin"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.emailLogin}
              touched={touched.emailLogin}
              errors={errors.emailLogin}
            />

            <InputPassword
              name="passwordLogin"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.passwordLogin}
              touched={touched.passwordLogin}
              errors={errors.passwordLogin}
              fp={true}
            />
            <div className="uk-width-1-1 mt-4">
              <button
                disabled={!isValid}
                type="button"
                onClick={(e) => {
                  e.preventDefault();
                  handleSubmit();
                }}
                className="uk-width-1-1 uk-button uk-button-success-outline"
              >
                Войти
              </button>
            </div>
            <hr className="uk-divider-icon"></hr>
            <div>
              <button
                onClick={(e) => {
                  e.preventDefault();
                  googleSignIn().then(() => {
                    navigate("/");
                  });
                }}
                className="uk-width-1-1 uk-button uk-button-success-outline uk-button-google"
              ></button>
            </div>
            <div className="mt-3">
              <p className="uk-text-small uk-text-center">
                Нет учетной записи?{" "}
                <Link className="forgot-link" to="/register">
                  Зарегистрироваться
                </Link>{" "}
              </p>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default Login;
