import React from "react";
import { Link } from "react-router-dom";

const MovieItem = ({
  tab,
  id,
  className,
  img,
  title,
  quality,
  isNew,
  isFree,
  isSerial
}) => {
  return (
    <div className={`movie-card ${className}`}>
      <Link
        to={"/watch/" + tab + id}
        state={{isSerial}}
        className="card-image-wrapper"
      >
        <div className="card-image">
          <img src={img} alt={title} />
          <div className="card-slots uk-position-absolute uk-position-top-left">
            <div className="uk-flex uk-flex-column uk-flex-top">
              {isFree && (
                <span className="uk-label uk-label-success uk-text-small uk-text-semibold px-3 mt-1 uk-border-rounded">
                  Бесплатно
                </span>
              )}
              {isNew && (
                <span className="uk-label uk-label-danger uk-text-small uk-text-semibold px-3 mt-1 uk-border-rounded">
                  Новое
                </span>
              )}
              {quality === "hd" && (
                <span className="uk-label uk-label-success uk-text-small uk-text-semibold px-3 mt-1 uk-border-rounded">
                  HD
                </span>
              )}
              {quality === "full_hd" && (
                <span className="uk-label uk-text-small uk-text-semibold px-3 mt-1 uk-border-rounded">
                  Full HD
                </span>
              )}
            </div>
          </div>
        </div>
        <div className="card-info mt-2">{title}</div>
      </Link>
    </div>
  );
};

export default MovieItem;
