import React from "react";
import Avatar from "react-avatar";
import { LgToXl, Md, Sm } from "./../useMedia";
import { logOut } from "./../../utils/firebaseAuth";

const Content = ({ name, email }) => {
  return (
    <div className="uk-card bg-dark-500 uk-light p-4 card-shadow">
      <div className="uk-flex uk-flex-middle uk-flex-left">
        <Avatar
          className="user-select-none"
          name={name}
          color="#06C270"
          size="50"
          maxInitials={2}
          textSizeRatio={2.5}
          round
        />
        <div className="uk-flex uk-flex-column ml-3">
          <span className="uk-text-semibold">{name}</span>
          <span className="uk-text-small">{email}</span>
        </div>
      </div>

      <ul className="uk-nav uk-nav-default mt-3">
        <li className="uk-nav-divider"></li>
        <li>
          <a
            onClick={() => {
              logOut().then(() => {
                window.location.reload()
              });
            }}
            href="#logOut"
          >
            Выйти
          </a>
        </li>
      </ul>
    </div>
  );
};

const Profile = ({ name, email }) => {
  return (
    <div className="uk-inline ">
      <LgToXl>
        <div className="profile-img cursor-pointer mr-2 mr-lg-4">
          <Avatar
            className="user-select-none"
            name="Rixsiboyev Sirojiddin"
            color="#06C270"
            size="38"
            maxInitials={2}
            textSizeRatio={3}
            round
          />
        </div>
        <div uk-drop="mode: click; animation: uk-animation-slide-top-small; duration: 500; pos: bottom-right">
          <Content name={name} email={email} />
        </div>
      </LgToXl>
      <Md>
        <div>
          <a className="uk-navbar-toggle" href="##">
            <span className="far fa-lg fa-user"></span>
          </a>
          <div uk-drop="mode: click; animation: uk-animation-slide-top-small; duration: 500; pos: bottom-right;offset:4">
            <Content name={name} email={email} />
          </div>
        </div>
      </Md>
      <Sm>
        <div>
          <a className="uk-navbar-toggle" href="##">
            <span className="far fa-lg fa-user"></span>
          </a>
          <div
            className="mt-n1"
            uk-drop="mode: click; animation: uk-animation-slide-top-small; duration: 500;boundary: .uk-navbar;boundary-align: true; pos: bottom-justify"
          >
            <Content name={name} email={email} />
          </div>
        </div>
      </Sm>
    </div>
  );
};

export default Profile;
