import React, { useState, useEffect } from "react";
import Slideshow from "../components/Slideshow/Slideshow";
import Slider from "../components/Slider/Slider";
import PageLoading from "./../components/Loading/PageLoading";
import { getHome } from "./../utils/api";

const Home = () => {
  const [data, setData] = useState(null);
  const [pageLoading, setPageLoading] = useState(true);

  useEffect(() => {
    getHome(setData).then(() => {
      setPageLoading(false);
    });
  }, []);

  return (
    <>
      <PageLoading loading={pageLoading} />
      <Slideshow />
      <Slider items={data && data[3]} title={data && data[3].title} tab={data && data[3].moduleId} />
      <Slider items={data && data[0]} title={data && data[0].title} tab={data && data[0].moduleId} />
      <Slider items={data && data[2]} title={data && data[2].title} tab={data && data[2].moduleId} tv={true}/>
      <Slider items={data && data[4]} title={data && data[4].title} tab={data && data[4].moduleId} />
      <Slider items={data && data[6]} title={data && data[6].title} tab={data && data[6].moduleId} />
    </>
  );
};

export default Home;
