import React from "react";
import { Formik } from "formik";
import * as yup from "yup";
import InputText from "./../components/Form/InputText";
import { resetPassword } from "../utils/firebaseAuth";

const ForgotPassword = () => {
  let schema = yup.object().shape({
    emailForgotPassword: yup
      .string()
      .email("Неверный адрес электронной почты")
      .required("Данное поле является обязательным"),
  });
  return (
    <div className="uk-position-fixed uk-width-1-1 uk-height-viewport uk-position-top-left login-page">
      <Formik
        initialValues={{
          emailForgotPassword: "",
        }}
        validateOnBlur
        onSubmit={({ emailForgotPassword }) => {
         resetPassword(emailForgotPassword).then(()=>{
           console.log("Link sended")
         })
        }}
        validationSchema={schema}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isValid,
        }) => (
          <form className="uk-form-stacked">
            <h2 className="uk-light uk-text-semibold">Сброс пароля</h2>
            <p className="mb-4">
              Введите адрес электронной почты,
              <br /> связанный с вашей учетной записью.
            </p>
            <InputText
              label="E-mail"
              placeholder="Введите ваш e-mail адрес"
              name="emailForgotPassword"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.emailForgotPassword}
              touched={touched.emailForgotPassword}
              errors={errors.emailForgotPassword}
            />

            <div className="uk-width-1-1 mt-4">
              <button
                disabled={!isValid}
                type="button"
                onClick={(e) => {
                  e.preventDefault();
                  handleSubmit();
                }}
                className="uk-width-1-1 uk-button uk-button-success-outline"
              >
                Продолжать
              </button>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default ForgotPassword;
