import axios from "axios";

export const getHome = (setState) => {
  return new Promise((res, rej) => {
    axios
      .get(`https://api.cinerama.uz/v2/web/home`)
      .then((response) => {
        setState(response.data.data);
        res("Success");
      })
      .catch((error) => {
        console.error(error);
        rej(error);
      });
  });
};

export const getMovies = (limit, tab, page, setState) => {
  return new Promise((res, rej) => {
    axios
      .get(
        `https://api.cinerama.uz/v2/web/content/movies/list?items=${limit}&module_id=${tab}&page=${page}`
      )
      .then((response) => {
        setState(response.data.data);
        res("Success");
      })
      .catch((error) => {
        console.error(error);
        rej(error);
      });
  });
};

