import React, { useEffect, useState } from "react";
import { useParams, useLocation } from "react-router";
import { getMovies } from "./../utils/api";
import { MdToXl, Sm } from "./../components/useMedia";
import Pagination from "rc-pagination";
import MovieItem from "./../components/MovieItem/MovieItem";
import PageLoading from "./../components/Loading/PageLoading";

const Movies = () => {
  const { id } = useParams();
  const { state } = useLocation();
  
  const [loading, setLoading] = useState(true);
  const [items, setItems] = useState([]);
  const [page, setPage] = useState(1);

  useEffect(() => {
    setLoading(true);
    getMovies(30, id, 1, setItems).then(() => {
      setLoading(false);
    });
  }, [id]);

  const pageChange = (page) => {
    setPage(page);
    setLoading(true);
    getMovies(30, id, page, setItems).then(() => {
      setLoading(false);
    });
  };

  return (
    <>
      <PageLoading loading={loading} />
      <MdToXl>
        <div className="uk-flex uk-flex-between m-3 m-md-4">
          <h3 className="m-0 uk-text-semibold uk-heading-bullet title">
            {state}
          </h3>
          <Pagination
            onChange={pageChange}
            current={page}
            pageSize={30}
            total={items.total}
          />
        </div>
      </MdToXl>
      <Sm>
        <div className="uk-flex uk-flex-column uk-flex-middle m-3">
          <h3 className="mb-3 uk-text-semibold title">{state}</h3>
          <Pagination
            onChange={pageChange}
            current={page}
            pageSize={30}
            total={items.total}
          />
        </div>
      </Sm>
      <div className="m-3 m-md-4" data-uk-height-viewport="expand: true">
        <ul className="uk-grid  uk-child-width-1-2  uk-child-width-1-4@s uk-child-width-1-5@m uk-child-width-1-6@l">
          {items.movies &&
            items.movies.map((el) => (
              <li key={el.id}>
                <MovieItem
                  id={el.id}
                  tab={id}
                  className="mb-3"
                  img={el.poster}
                  title={el.title}
                  year={el.year}
                  quality={el.quality}
                  isNew={el.isNew}
                  isFree={el.isFree}
                  isSerial = {el.isSerial}
                />
              </li>
            ))}
        </ul>
      </div>
      <div className="uk-flex uk-flex-right@m uk-flex-center m-3">
        <Pagination
          onChange={pageChange}
          current={page}
          pageSize={30}
          total={items.total}
        />
      </div>
    </>
  );
};

export default Movies;
