import React from "react";
import { Link } from "react-router-dom";
const items = [
  { name: "Телевидение", icon: "fa-tv-alt", link: "", tv: true },
  { name: "Фильмы", icon: "fa-film-alt", link: "3" },
  { name: "Сериалы", icon: "fa-film", link: "4" },
  { name: "Мультфильмы", icon: "fa-alicorn", link: "5" },
  { name: "Аниме", icon: "fa-user-ninja", link: "7" },
];

const Offcanvas = () => {
  return (
    <div id="offcanvas-flip" uk-offcanvas="flip: true; overlay: true">
      <div className="uk-offcanvas-bar bg-dark-500 uk-flex uk-flex-column uk-flex-top uk-flex-between">
        <button
          className="uk-offcanvas-close"
          type="button"
          data-uk-close
        ></button>

        <ul className="uk-nav uk-nav-primary mt-5 uk-width-1-1">
          {items.map((el, i) => {
            return (
              <li className="uk-flex-inline uk-flex-middle" key={el.name + i}>
                <span
                  style={{ width: 40 }}
                  className={`uk-text-center mr-2 far fa-lg ${el.icon}`}
                ></span>
                <Link state={el.name} to={el.tv ? "/tv/28" : "/movies/" + el.link}>
                  {el.name}
                </Link>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};

export default Offcanvas;
