import React, { useState } from "react";
import { NavLink } from "react-router-dom";

const InputPassword = ({ fp = false, touched, errors, ...props }) => {
  const [isVisible, setIsVisible] = useState(false);

  return (
    <div className="uk-margin uk-light">
      <div className="uk-flex uk-flex-between">
        <label className="uk-form-label" htmlFor={props.name}>
          Пароль
        </label>
        {fp && (
          <NavLink to="/forgotPassword" className="uk-form-label forgot-link">
            Забыли пароль?
          </NavLink>
        )}
      </div>

      <div className="uk-form-controls">
        <div className="uk-inline">
          <a
            href="##"
            className="uk-form-icon uk-form-icon-flip eye"
            onClick={() => {
              setIsVisible(!isVisible);
            }}
          >
            <i className={`fa ${isVisible ? "fa-eye-slash" : "fa-eye"}`}></i>
          </a>
          <input
            className={`uk-input ${
              touched && errors ? "uk-form-danger" : "uk-form-light"
            }`}
            type={isVisible ? "text" : "password"}
            placeholder="Введите пароль"
            {...props}
          />
        </div>
      </div>
      {touched && errors && <span className="uk-text-danger uk-text-small">{errors}</span>}
    </div>
  );
};

export default InputPassword;
