import React from "react";
import { MdToXl } from "./../useMedia";
import { Link } from "react-router-dom";

const items = [
  { name: "Телевидение", tv: true, link: "" },
  { name: "Фильмы", link: "3" },
  { name: "Сериалы", link: "4" },
  { name: "Мультфильмы", link: "5" },
  { name: "Аниме", link: "7" },
];

const List = () => {
  return (
    <div className="uk-light">
      <MdToXl>
        <ul className="uk-navbar-nav">
          {items.map((el, i) => {
            return (
              <li key={el.name + i}>
                <Link
                  state={el.name}
                  to={el.tv ? "/tv/28" : "/movies/" + el.link}
                >
                  {el.name}
                </Link>
              </li>
            );
          })}
        </ul>
      </MdToXl>
    </div>
  );
};

export default List;
