import React from "react";

const TvListItem = ({ image, title, isFree, isHd, poster, setPoster }) => {
  return (
    <div
      className={`uk-flex uk-flex-middle uk-position-relative tv-list-item mb-3 ${
        poster === image ? "played" : ""
      }`}
      onClick={() => {
        isFree && setPoster(image);
      }}
    >
      <div className={`uk-inline ${!isFree ? "block" : ""}`}>
        <img className="chanell-icon" src={image} alt="" />
      </div>
      <a href="#!" className="chanell-name">
        {title}
      </a>
      {isHd && (
        <span className="uk-position-absolute uk-position-top-left uk-label uk-label-success uk-text-small uk-text-semibold px-3 mt-1 uk-border-rounded">
          HD
        </span>
      )}
    </div>
  );
};

export default TvListItem;
