import React from "react";
import { useNavigate } from "react-router";

const TvItem = ({ id, image, title, isFree }) => {
  const navigate = useNavigate();
  return (
    <div
      className="tv-items uk-box-shadow-hover-large"
      onClick={() => {
        isFree && navigate("/tv/" + id);
      }}
    >
      <div className={`uk-inline ${!isFree ? "block" : ""}`}>
        <img className="uk-border-rounded" src={image} alt={title} />
      </div>
    </div>
  );
};

export default TvItem;
