import React from "react";

const SerialsItem = ({ classNames, image, episode, poster, setPoster }) => {
  return (
    <div
      className={`${classNames} ${poster === image ? "played" :""}`}
      onClick={() => {
        setPoster(image);
      }}
    >
      <div className="uk-inline">
        <img className="uk-border-rounded" src={image} alt={episode} />
        <div className="uk-overlay uk-overlay-primary uk-position-bottom">
          <p>{episode + ". Серия"}</p>
        </div>
      </div>
    </div>
  );
};

export default SerialsItem;
