import React, { useState } from "react";
import { LgToXl, MdAngBelow } from "./../useMedia";
import { useNavigate } from "react-router-dom";

const Search = () => {
  const navigate = useNavigate();
  const [query, setQuery] = useState("");
  return (
    <>
      <LgToXl>
        <div className="mr-2 mr-md-4 ml-2">
          <form
            className="uk-search uk-search-default"
            onSubmit={(e) => {
              e.preventDefault();
              query.length > 0 && navigate("/search/" + query);
              query.length > 0 && setQuery("");
            }}
          >
            <a
              href="#search"
              onClick={() => {
                query.length > 0 && setQuery("");
                query.length > 0 && navigate("/search/" + query);
              }}
              data-uk-search-icon
            >{""}</a>
            <input
              className="uk-search-input"
              type="search"
              placeholder="Поиск..."
              value={query}
              onChange={(e) => {
                setQuery(e.currentTarget.value);
              }}
            />
          </form>
        </div>
      </LgToXl>
      <MdAngBelow>
        <a
          className="uk-navbar-toggle"
          data-uk-toggle="target: .nav-overlay; animation: uk-animation-fade"
          href="#search"
        >
          <span className="far fa-lg fa-search"></span>
        </a>
      </MdAngBelow>
    </>
  );
};

export default Search;
