import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import { getSearch } from "./../utils/api";
import { MdToXl, Sm } from "./../components/useMedia";
import Pagination from "rc-pagination";
import MovieItem from "./../components/MovieItem/MovieItem";
import PageLoading from "./../components/Loading/PageLoading";

const SearchView = () => {
  const { query } = useParams();

  const [loading, setLoading] = useState(true);
  const [items, setItems] = useState([]);
  const [page, setPage] = useState(1);

  useEffect(() => {
    setLoading(true);
    getSearch(30, 1, query, setItems).then(() => {
      setLoading(false);
    });
  }, [query]);

  const pageChange = (page) => {
    setPage(page);
    setLoading(true);
    getSearch(30, page, query, setItems).then(() => {
      setLoading(false);
    });
  };

  return (
    <>
      <PageLoading loading={loading} />
      {items.movies && items?.movies.length > 0 ? (
        <div>
          <MdToXl>
            <div className="uk-flex uk-flex-between m-3 m-md-4">
              <h3 className="m-0 uk-text-semibold uk-heading-bullet title">
                Результаты поиска по запросу "{query}"
              </h3>
              <Pagination
                onChange={pageChange}
                current={page}
                pageSize={30}
                total={items.total}
              />
            </div>
          </MdToXl>
          <Sm>
            <div className="uk-flex uk-flex-column uk-flex-middle m-3">
              <h3 className="mb-3 uk-text-semibold title">
                Результаты поиска по запросу "{query}"
              </h3>
              <Pagination
                onChange={pageChange}
                current={page}
                pageSize={30}
                total={items.total}
              />
            </div>
          </Sm>
          <div className="m-3 m-md-4" data-uk-height-viewport="expand: true">
            <ul className="uk-grid  uk-child-width-1-2  uk-child-width-1-4@s uk-child-width-1-5@m uk-child-width-1-6@l">
              {items.movies.map((el) => (
                <li key={el.id}>
                  <MovieItem
                    id={el.id}
                    tab={el.moduleId}
                    className="mb-3"
                    img={el.poster}
                    title={el.title}
                    year={el.year}
                    quality={el.quality}
                    isNew={el.isNew}
                    isFree={el.isFree}
                    isSerial={el.isSerial}
                  />
                </li>
              ))}
            </ul>
          </div>
          <div className="uk-flex uk-flex-right@m uk-flex-center m-3">
            <Pagination
              onChange={pageChange}
              current={page}
              pageSize={30}
              total={items.total}
            />
          </div>
        </div>
      ) : (
        <div
          className="m-3 m-md-4 not-found"
          data-uk-height-viewport="expand: true"
        >
          <h3 className="m-0 uk-text-semibold title">Ничего не найдено</h3>
        </div>
      )}
    </>
  );
};

export default SearchView;
