import React from "react";
import { Formik } from "formik";
import { Link } from "react-router-dom";
import * as yup from "yup";
import InputText from "./../components/Form/InputText";
import InputPassword from "./../components/Form/InputPassword";
import { signUp, googleSignIn } from "../utils/firebaseAuth";
import { useNavigate } from "react-router";

const Register = () => {
  let schema = yup.object().shape({
    loginRegister: yup
      .string()
      .min(3, "Должно быть не менее 3 символов")
      .matches(/^[A-zА-я0-9_ ]+$/, "Удалите ненужные символы")
      .required("Данное поле является обязательным"),
    emailRegister: yup
      .string()
      .email("Неверный адрес электронной почты")
      .required("Данное поле является обязательным"),
    passwordRegister: yup
      .string()
      .min(6, "Должно быть не менее 6 символов")
      .matches(/^[A-zА-я0-9_ ]+$/, "Удалите ненужные символы")
      .required("Данное поле является обязательным"),
  });

  const navigate = useNavigate();

  return (
    <div className="uk-position-fixed uk-width-1-1 uk-height-viewport uk-position-top-left login-page">
      <Formik
        initialValues={{
          loginRegister: "",
          emailRegister: "",
          passwordRegister: "",
        }}
        validateOnBlur
        onSubmit={({ loginRegister, emailRegister, passwordRegister }) => {
          signUp(loginRegister, emailRegister, passwordRegister).then(() => {
            console.log("Sign Up");
            navigate("/");
          });
        }}
        validationSchema={schema}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isValid,
        }) => (
          <form className="uk-form-stacked m-3 uk-overflow-auto">
            <h2 className="uk-light uk-text-semibold">Регистрация</h2>
            <InputText
              label="Имя"
              placeholder="Введите ваше имя"
              name="loginRegister"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.loginRegister}
              touched={touched.loginRegister}
              errors={errors.loginRegister}
            />

            <InputText
              label="E-mail"
              placeholder="Введите ваш e-mail адрес"
              name="emailRegister"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.emailRegister}
              touched={touched.emailRegister}
              errors={errors.emailRegister}
            />

            <InputPassword
              name="passwordRegister"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.passwordRegister}
              touched={touched.passwordRegister}
              errors={errors.passwordRegister}
            />
            <div className="uk-width-1-1 mt-4">
              <button
                disabled={!isValid}
                type="button"
                onClick={(e) => {
                  e.preventDefault();
                  handleSubmit();
                }}
                className="uk-width-1-1 uk-button uk-button-success-outline"
              >
                Зарегистрироваться
              </button>
            </div>
            <hr className="uk-divider-icon"></hr>
            <div>
              <button
                onClick={(e) => {
                  e.preventDefault();
                  googleSignIn().then(() => {
                    navigate("/");
                  });
                }}
                className="uk-width-1-1 uk-button uk-button-success-outline uk-button-google"
              ></button>
            </div>
            <div className="mt-3">
              <p className="uk-text-small uk-text-center">
                Уже есть аккаунт ?{" "}
                <Link className="forgot-link" to="/login">
                  Войти
                </Link>{" "}
              </p>
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default Register;
