import React, { memo } from "react";
import { Link } from "react-router-dom";
import MovieItems from "../MovieItem/MovieItem";
import TvItem from "../MovieItem/TvItem";

const Slider = memo(({ items, title, tab, tv }) => {
  return (
    <div className="m-3 m-md-4 mb-4 mb-md-5">
      <div
        className="uk-position-relative uk-visible-toggle"
        tabIndex="-1"
        data-uk-slider="finite: true; sets: true"
      >
        <div className="uk-flex uk-flex-middle uk-flex-between mb-4">
          <h3 className="m-0 uk-text-semibold uk-heading-bullet">
            <Link
              state={title}
              className="title"
              to={tv ? "/tv/28" : "/movies/" + tab}
            >
              {title}
            </Link>
          </h3>
          <div className="uk-flex uk-flex-middle">
            <a
              href="#previous"
              className="slider-btn uk-button-success-outline"
              uk-slider-item="previous"
            >
              <span className="fal fa-arrow-left"></span>
            </a>
            <a
              href="#next"
              className="slider-btn uk-button-success-outline"
              uk-slider-item="next"
            >
              <span className="fal fa-arrow-right"></span>
            </a>
          </div>
        </div>
        <ul
          className="uk-slider-items uk-child-width-1-2  uk-child-width-1-4@s uk-child-width-1-5@m uk-child-width-1-6@l uk-grid"
          uk-height-match="target: > li > div > .card-image-wrapper > div > img; row: false"
        >
          {items?.list
            ? items?.list.map((el) => (
                <li key={el.id}>
                  {tv ? (
                    <TvItem id={el.id} image={el.channelIcon} title={el.title} isFree={el.isFree} />
                  ) : (
                    <MovieItems
                      tab={tab}
                      id={el.id}
                      img={el.poster}
                      title={el.title}
                      year={el.year}
                      quality={el.quality}
                      isNew={el.isNew}
                      isFree={el.isFree}
                    />
                  )}
                </li>
              ))
            : null}
        </ul>
      </div>
    </div>
  );
});

export default Slider;
