import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router";
import { getTV } from "./../utils/api";
import PageLoading from "../components/Loading/PageLoading";
import { DefaultPlayer as Video } from "react-html5video";
import { MdAngBelow, LgToXl } from "../components/useMedia";
import TvListItem from "./../components/MovieItem/TvListItem";
import { Link } from "react-router-dom";
import { Context } from "./../App";
const defaultMovie = "https://files.itv.uz/assets/help/subscription_needed.mp4";

const Tv = () => {
  const { id } = useParams();
  const { user } = useContext(Context);
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState(null);
  const [poster, setPoster] = useState(null);

  useEffect(() => {
    getTV(15, setData).then(() => {
      setLoading(false);
    });
  }, []);

  useEffect(() => {
    let defaultPoster;

    data &&
      data.forEach((el) => {
        if (el.id === +id && el.isFree) {
          defaultPoster = el.channelIcon;
          return defaultPoster;
        }
      });

    setPoster(defaultPoster);
  }, [id, data]);

  return (
    <div>
      <PageLoading loading={loading} />

      <div className="m-3 m-md-4" data-uk-height-viewport="expand: true">
        <div className="uk-flex uk-child-width-1-1 video-content">
          {user ? (
            <Video
              className="uk-width-auto"
              controls={["PlayPause", "Seek", "Time", "Volume", "Fullscreen"]}
              poster={poster}
              onCanPlayThrough={() => {}}
            >
              <source src={defaultMovie} />
            </Video>
          ) : (
            <div className="no-auth-poster-tv">
              <img src={poster} alt="poster" />
              <Link to="/login">
                <span className="fa fa-2x fa-play"></span>
              </Link>
            </div>
          )}
          {data && (
            <LgToXl>
              <div className="uk-width-1-3@s uk-width-1-1 serial-items-block p-3">
                {data &&
                  data.map((el) => (
                    <TvListItem
                      key={el.id}
                      id={el.id}
                      image={el.channelIcon}
                      title={el.title}
                      isFree={el.isFree}
                      isHd={el.isHd}
                      poster={poster}
                      setPoster={setPoster}
                    />
                  ))}
              </div>
            </LgToXl>
          )}
        </div>

        {data && (
          <MdAngBelow>
            <div className="uk-light pt-3 px-3 movie-info">
              <ul className="mb-0" uk-accordion="multiple: true">
                <li>
                  <a className="uk-accordion-title pb-3" href="#open">
                    Все каналы
                  </a>
                  <div className="uk-accordion-content toggle-serial-items-block uk-child-width-1-1 uk-child-width-1-2@s uk-grid">
                    {data &&
                      data.map((el) => (
                        <div key={el.id}>
                          <TvListItem
                            id={el.id}
                            image={el.channelIcon}
                            title={el.title}
                            isFree={el.isFree}
                            isHd={el.isHd}
                            poster={poster}
                            setPoster={setPoster}
                          />
                        </div>
                      ))}
                  </div>
                </li>
              </ul>
            </div>
          </MdAngBelow>
        )}
      </div>
    </div>
  );
};

export default Tv;
