import React from "react";
import { LgToXl, MdAngBelow } from "./../useMedia";
import { useNavigate } from "react-router";
import { Link } from "react-router-dom";

const Button = () => {
  const navigate = useNavigate();
  return (
    <div>
      <LgToXl>
        <button
          className="mr-2 mr-md-4 uk-button uk-button-success-outline"
          onClick={() => {
            navigate("/login");
          }}
        >
          Войти
        </button>
      </LgToXl>
      <MdAngBelow>
        <Link className="uk-navbar-toggle" to="/login">
          <span className="far fa-lg fa-user"></span>
        </Link>
      </MdAngBelow>
    </div>
  );
};

export default Button;
