import React, { useContext, useState } from "react";
import { Sm } from "../useMedia";
import Logo from "./Logo";
import List from "./List";
import Search from "./Search";
import Button from "./Button";
import Profile from "./Profile";
import Offcanvas from "./Offcanvas";
import { Context } from "./../../App";
import { useNavigate } from "react-router";

const Navbar = () => {
  const { user } = useContext(Context);
  const navigate = useNavigate();
  const [query, setQuery] = useState("");
  return (
    <>
      <nav
        className="uk-navbar-container uk-flex-nowrap"
        uk-navbar="mode: click"
        data-uk-sticky
      >
        <div className="uk-navbar-left nav-overlay uk-flex-nowrap">
          <Logo />
          <List />
        </div>
        <div className="uk-navbar-right nav-overlay uk-flex-nowrap">
          <Search />
          {user ? (
            <Profile name={user.displayName} email={user.email} />
          ) : (
            <Button />
          )}
          <Sm>
            <a
              className="uk-navbar-toggle"
              href="#menu"
              uk-toggle="target: #offcanvas-flip"
            >
              <span className="far fa-lg fa-bars"></span>
            </a>
          </Sm>
        </div>

        <div className="nav-overlay uk-navbar-left uk-flex-1" hidden>
          <div className="uk-navbar-item uk-width-expand">
            <form
              className="uk-search uk-search-navbar uk-width-1-1"
              onSubmit={(e) => {
                e.preventDefault();
                query.length > 0 && navigate("/search/" + query);
                query.length > 0 && setQuery("");
              }}
            >
              <input
                className="uk-search-input"
                type="search"
                placeholder="Search"
                value={query}
                onChange={(e) => {
                  setQuery(e.currentTarget.value);
                }}
                autoFocus
              />
            </form>
          </div>

          <a
            className="uk-navbar-toggle"
            data-uk-close
            data-uk-toggle="target: .nav-overlay; animation: uk-animation-fade"
            href="#search"
          >
            {" "}
          </a>
        </div>
      </nav>
      <Offcanvas />
    </>
  );
};

export default Navbar;
