import React from "react";

const NotFound = () => {
  return (
    <div
      className="m-3 m-md-4 not-found"
      data-uk-height-viewport="expand: true"
    >
      <h1 className="m-0 uk-heading-xlarge uk-light uk-text-semibold">404</h1>
      <h3 className="m-0 uk-light uk-text-semibold">Страница не найдена</h3>
    </div>
  );
};

export default NotFound;
